/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* CSS RESET ABOVE */
*,
html,
body {
  font-family: "Noto Sans JP", sans-serif;
}
.nav-links {
  color: white;
  font-size: 24px;
  text-align: center;

  text-decoration: none;
  list-style: none;
}

.nav-bar {
  height: 48px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.searchbar {
  width: 80%;
  background: white;
}

.search-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 20px;
}

.card-image {
  margin: 0 auto;
}

.shadow {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  margin: 20px;
  margin-top: 0;
}

h1 {
  font-size: 48px;
  font-weight: 800px;
}

.margin-top {
  margin-top: 40px;
}
.dropdown {
  padding: 5px 20px;
  z-index: 1;
  position: absolute;
  background-color: white;
  width: 70%;
  top: 100px;
  border: 1px solid lightgrey;
}

.dropdown li {
  border-bottom: 1px solid lightgray;
  padding: 10px 0px;
}
